import React, { useEffect, useRef, useState } from "react";
import "./header.css";
import SubAouController from "./subaouController";
import Router from "next/router";
import getUserType from "../../utils/userType";
import isLogin from "../../utils/isLogin";

const Header = () => {
  const [isLoggedIn, setLoginStatus] = useState(false);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const { fname, email, icon, name, userType } = getUserType();
  const [mobileDropdown, setMobileDropdown] = useState(false);
  const toggleModal = () => {
    setIsModalOpen(!isModalOpen);
  };
  useEffect(() => {
    setLoginStatus(isLogin());
    
  }, []);
  const navigateTo = (path) => {
    Router.push(path);
  };
  const logout = () => {
    localStorage.removeItem("hoarTemplatetoken");
    localStorage.removeItem("userInfo");
    localStorage.removeItem("requestedPlan");
    setLoginStatus(false);
    navigateTo("/");
  };
  return (
    <>
      <div className="site_header">
        <div className="site_header_logo" onClick={() => navigateTo("/")}>
          <img src="/assets/images/logo/3.png" alt="Logo" />
        </div>
        <div className="site_header_items">
          <div key="contact" className="contact_icon dropdown_hover">
            <div className="menu_dropdown contact_dropdown">
              <div className="up_arr_account"></div>
              {/* custom widgets here */}
              <div
                className="dropdown_item"
                onClick={() => navigateTo("/layouts/contact")}
              >
                <div
                  style={{
                    display: "flex",
                  }}
                >
                  <p
                    className="hover_scale"
                    style={{
                      fontSize: "17px",
                      whiteSpace: "nowrap",
                    }}
                  >
                    Technical
                  </p>
                </div>
              </div>
              <div
                className="dropdown_item"
                onClick={() => navigateTo("/layouts/contact#queryQuestion")}
              >
                <div
                  style={{
                    display: "flex",
                  }}
                >
                  <p
                    style={{
                      fontSize: "17px",
                      whiteSpace: "nowrap",
                    }}
                    className="hover_scale"
                  >
                    Get in Touch
                  </p>
                </div>
              </div>
            </div>
            <div style={{ width: "40px", height: "40px" }}>
              <img
                style={{ width: "100%", height: "100%" }}
                src="/assets/images/question.png"
              />
            </div>
          </div>
          {isLoggedIn && userType !== "Please choose a Plan!" && (
            <div
              key="rules"
              className="rules_icon"
              onClick={() => navigateTo("/layouts/category")}
            >
              <img
                src="/assets/images/book.png"
                style={{
                  width: "25px",
                  height: "25px",
                  position: "relative",
                  top: "-1px",
                  left: "-5px",
                }}
              />
              <p>Create Rules!</p>
            </div>
          )}
          {isLoggedIn && userType === "Community Administrator" && (
            <div key="subaou" className="subaou_modal">
              <a title="Add Additional Users">
                {" "}
                <div
                  onClick={toggleModal}
                  style={{ width: "40px", height: "40px" }}
                >
                  <img
                    style={{ width: "100%", height: "100%" }}
                    src="/assets/images/add-user.png"
                  />
                </div>
              </a>
            </div>
          )}

          {!isLoggedIn && (
            <div
              key="pricing"
              className="pricing_icon rules_icon"
              style={{ paddingRight: "20px" }}
              onClick={() => navigateTo("/layouts/pricing")}
            >
              <img
                src="/assets/images/tag.png"
                style={{
                  width: "25px",
                  height: "25px",
                  position: "relative",
                  top: "-1px",
                  left: "-5px",
                }}
              />
              <p>Pricing</p>
            </div>
          )}
          {isLoggedIn && (
            <div key="account" className="account_icon dropdown_hover">
              <div className="menu_dropdown account_dropdown">
                <div className="up_arr_account"></div>
                {/* custom widgets here */}
                <div className="dropdown_item">
                  <div style={{ height: "40px", width: "40px" }}>
                    <img
                      style={{ height: "100%", width: "100%" }}
                      src="/assets/images/user.png"
                      alt="IC"
                    />
                  </div>
                  <div
                    style={{
                      display: "flex",
                      flexDirection: "column",
                      marginLeft: "15px",
                    }}
                  >
                    <p
                      style={{
                        marginBottom: "1px",
                        color: "black",
                        fontSize: "17px",
                        whiteSpace: "nowrap",
                      }}
                    >
                      {"Welcome! " + name}
                    </p>
                    <p
                      style={{
                        fontSize: "13px",
                        whiteSpace: "nowrap",
                        color: "rgba(0,0,0,0.6)",
                      }}
                    >
                      {userType}
                    </p>
                  </div>
                </div>
                <div
                  className="dropdown_item"
                  onClick={() => navigateTo("/layouts/profile")}
                >
                  <div
                    style={{
                      height: "40px",
                      width: "40px",
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                    }}
                  >
                    <img
                      style={{ height: "50%", width: "50%" }}
                      src="/assets/images/account.png"
                      alt="IC"
                    />
                  </div>
                  <div
                    style={{
                      display: "flex",
                      flexDirection: "column",
                      marginLeft: "15px",
                    }}
                  >
                    <p
                      style={{
                        fontSize: "17px",
                      }}
                      className="hover_scale"
                    >
                      Account
                    </p>
                  </div>
                </div>
                <div className="dropdown_item">
                  <div
                    style={{
                      height: "40px",
                      width: "40px",
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                    }}
                  >
                    <img
                      style={{ height: "50%", width: "50%" }}
                      src="/assets/images/logout.png"
                      alt="IC"
                    />
                  </div>
                  <div
                    onClick={logout}
                    style={{
                      display: "flex",
                      flexDirection: "column",
                      marginLeft: "15px",
                    }}
                  >
                    <p
                      style={{
                        fontSize: "17px",
                      }}
                      className="hover_scale"
                    >
                      Logout
                    </p>
                  </div>
                </div>
              </div>
              <div className="account_details">
                <p className="user_name">{fname}</p>
                <p className="user_email">{email}</p>
              </div>
              <div className="account_avatar">{icon}</div>
            </div>
          )}
          {!isLoggedIn && (
            <div
              className="login_icon"
              key="login"
              onClick={() => navigateTo("/layouts/login")}
            >
              <img
                src="/assets/images/login.png"
                style={{
                  width: "30px",
                  height: "30px",
                  position: "relative",
                  top: "-5px",
                  left: "-5px",
                }}
              />
              <p>Login</p>
            </div>
          )}
        </div>
      </div>
      <div className="site_header_mobile">
        <div
          className="site_header_mobile_logo"
          onClick={() => navigateTo("/")}
        >
          <img src="/assets/images/logo/3.png" alt="Logo" />
        </div>

        <div
          tabIndex="0"
          className="site_header_mobile_dropdown"
          onFocus={() => {
            setMobileDropdown(true);
          }}
          onBlur={(e) => {
            if (!e.relatedTarget || e.relatedTarget.className !== "mb_href") {
              setMobileDropdown(false);
            }
          }}
        >
          <img
            style={{
              height: "2rem",
              width: "2rem",
            }}
            src="/assets/images/bars-solid.svg"
            alt="ic"
          />
        </div>
        {mobileDropdown && (
          <div className="site_header_mobile_content">
            {isLoggedIn && (
              <p
                onClick={() => navigateTo("/layouts/profile")}
                tabIndex="0"
                className="mb_href"
              >
                Account
              </p>
            )}
            {!isLoggedIn && (
              <p
                onClick={() => navigateTo("/layouts/login")}
                className="mb_href"
                tabIndex="0"
              >
                Login
              </p>
            )}
            {isLoggedIn && userType !== "Please choose a Plan!" && (
              <p
                onClick={() => navigateTo("/layouts/category")}
                className="mb_href"
                tabIndex="0"
              >
                Create Rules!
              </p>
            )}
            {!isLoggedIn && (
              <p
                onClick={() => navigateTo("/layouts/pricing")}
                className="mb_href"
                tabIndex="0"
              >
                Pricing
              </p>
            )}
            <p
              onClick={() => navigateTo("/layouts/contact")}
              className="mb_href"
              tabIndex="0"
            >
              Contact Us
            </p>
            {isLoggedIn && userType === "Community Administrator" && (
              <p onClick={toggleModal} className="mb_href" tabIndex="0">
                Add Additional Users
              </p>
            )}
            {isLoggedIn && (
              <p onClick={logout} className="mb_href" tabIndex="0">
                Logout
              </p>
            )}
          </div>
        )}
      </div>
      <SubAouController isModalOpen={isModalOpen} toggleModal={toggleModal} />
    </>
  );
};

export default Header;
